html {
  font-size: 62.3%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  color: white;
  min-width: 360px;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  height: 100vh;
}

.tweet {
  width: 100%;
  max-width: 50rem;
  border: solid 0.1rem white;
  display: flex;
  padding: 2rem;
  column-gap: 1rem;
  margin: 1rem;
}

.tweet__img {
  width: 10%;
  min-width: 4rem;
}
.tweet__img img {
  width: 100%;
}

.tweet__content {
  width: 90%;
}
.tweet__content h1 {
  display: inline-block;
  font-size: 1.2rem;
  margin-right: 0.2rem;
}
.tweet__content span {
  font-size: 1.2rem;
  color: grey;
  font-weight: 500;
}
.tweet__content p {
  font-size: 1.1rem;
  font-weight: 400;
  line-height: 2rem;
}
.tweet__content p a {
  color: dodgerblue;
  text-decoration: none;
}
.tweet__content p a:hover {
  text-decoration: underline;
}

.tweet__publicacion {
  display: flex;
  border: solid grey 0.1rem;
  border-radius: 20px;
  overflow: hidden;
  height: 10rem;
  width: 100%;
}
.tweet__publicacion .img {
  width: 25%;
  min-width: 10rem;
  background-image: url("./assets/grid-gallery.webp");
  background-size: contain;
  background-position: center;
}
.tweet__publicacion .publicacion__content {
  width: 75%;
  padding: 1.6rem;
}
.tweet__publicacion .publicacion__content h1 {
  margin: 0;
  font-weight: 500;
}
.tweet__publicacion .publicacion__content p::after {
  content: "LeonidasEsteban.com";
  font-size: 1.1rem;
  color: grey;
}
@media (min-width: 768px) {
  .tweet__publicacion .publicacion__content p::after {
    content: "Practica tus habilidades de Grid CSS con este ejercicio de las capitales del mundo.";
  }
}

.tweet__reaction {
  display: flex;
  align-items: center;
  margin: 1.6rem 1rem 0;
}

/*Variables*/
.tweet__heart {
  display: inline-block;
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1rem;
  width: 2rem;
  height: 2rem;
}

#heart {
  position: absolute;
  left: -100vw;
}

/* Corazon */
#heart + [for=heart] {
  font-size: 1.5rem;
  color: transparent;
  -webkit-text-stroke: 1px #aab8c2;
  cursor: pointer;
  user-select: none;
}

#heart:checked + [for=heart] {
  color: #e2264d;
  animation: heart 1s cubic-bezier(0.17, 0.89, 0.32, 1.49);
  -webkit-text-stroke: 1px #e2264d;
}

#heart + [for=heart] {
  position: relative;
}
#heart + [for=heart]::before, #heart + [for=heart]::after {
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  content: "";
}

/*  Burbuja */
#heart + [for=heart]::before {
  width: 4.5rem;
  height: 4.5rem;
  margin: -2.25rem;
  border: solid 2.25rem #e2264d;
  transform: scale(0);
}

#heart:checked + [for=heart]::before {
  animation: bubble 1.5s cubic-bezier(0.21, 0.61, 0.35, 1);
}

/* Particulas */
#heart + [for=heart]::after {
  margin: -0.1875rem;
  width: 0.375rem;
  height: 0.375rem;
  box-shadow: 0rem -2.8125rem 0 -0.20625rem #ff8080, 0.7796923734rem -2.9098515517rem 0 -0.20625rem #ffed80, 2.1989010444rem -1.7535650677rem 0 -0.20625rem #ffed80, 2.7611437958rem -1.2046747233rem 0 -0.20625rem #a4ff80, 2.741984753rem 0.6258401268rem 0 -0.20625rem #a4ff80, 2.6633976229rem 1.4076467427rem 0 -0.20625rem #80ffc8, 1.2202980163rem 2.533974941rem 0 -0.20625rem #80ffc8, 0.5600587166rem 2.9599815006rem 0 -0.20625rem #80c8ff, -1.2202980163rem 2.533974941rem 0 -0.20625rem #80c8ff, -1.9650158265rem 2.283389816rem 0 -0.20625rem #a480ff, -2.741984753rem 0.6258401268rem 0 -0.20625rem #a480ff, -3.0103933732rem -0.1126409728rem 0 -0.20625rem #ff80ed, -2.1989010444rem -1.7535650677rem 0 -0.20625rem #ff80ed, -1.788883309rem -2.4238508115rem 0 -0.20625rem #ff8080;
}

#heart:checked + [for=heart]::after {
  animation: particles 1.5s cubic-bezier(0.17, 0.89, 0.32, 1.49);
}

@keyframes heart {
  0%, 17.5% {
    font-size: 0;
  }
}
@keyframes bubble {
  15% {
    border-color: #cc8ef5;
    border-width: 2.25rem;
    transform: scale(1);
  }
  30%, 100% {
    border-color: #cc8ef5;
    border-width: 0;
    transform: scale(1);
  }
}
@keyframes particles {
  0%, 20% {
    box-shadow: 0rem -2.8125rem 0 -0.20625rem #ff8080, 0.7796923734rem -2.9098515517rem 0 -0.20625rem #ffed80, 2.1989010444rem -1.7535650677rem 0 -0.20625rem #ffed80, 2.7611437958rem -1.2046747233rem 0 -0.20625rem #a4ff80, 2.741984753rem 0.6258401268rem 0 -0.20625rem #a4ff80, 2.6633976229rem 1.4076467427rem 0 -0.20625rem #80ffc8, 1.2202980163rem 2.533974941rem 0 -0.20625rem #80ffc8, 0.5600587166rem 2.9599815006rem 0 -0.20625rem #80c8ff, -1.2202980163rem 2.533974941rem 0 -0.20625rem #80c8ff, -1.9650158265rem 2.283389816rem 0 -0.20625rem #a480ff, -2.741984753rem 0.6258401268rem 0 -0.20625rem #a480ff, -3.0103933732rem -0.1126409728rem 0 -0.20625rem #ff80ed, -2.1989010444rem -1.7535650677rem 0 -0.20625rem #ff80ed, -1.788883309rem -2.4238508115rem 0 -0.20625rem #ff8080;
  }
  25% {
    box-shadow: 0rem -2.25rem 0 0rem #ff8080, 0.6341066605rem -2.3665182744rem 0 0rem #ffed80, 1.7591208356rem -1.4028520542rem 0 0rem #ffed80, 2.2455775269rem -0.9797354596rem 0 0rem #a4ff80, 2.1935878024rem 0.5006721014rem 0 0rem #a4ff80, 2.1660827141rem 1.1448081393rem 0 0rem #80ffc8, 0.976238413rem 2.0271799528rem 0 0rem #80ffc8, 0.4554834375rem 2.4072878594rem 0 0rem #80c8ff, -0.976238413rem 2.0271799528rem 0 0rem #80c8ff, -1.5981041576rem 1.8570307217rem 0 0rem #a480ff, -2.1935878024rem 0.5006721014rem 0 0rem #a480ff, -2.4482867267rem -0.091608426rem 0 0rem #ff80ed, -1.7591208356rem -1.4028520542rem 0 0rem #ff80ed, -1.4548594547rem -1.9712645604rem 0 0rem #ff8080;
